.stocktable {
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto 60px;
  max-width: 800px;
}

.stocktable .table_title {
  color: green;
  text-align: left;
  margin-bottom: 10px;
}

.stocktable table {
  width: 100%;
  border-collapse: collapse;
}

.stocktable table th {
  padding: 5px 15px;
  color: gray;
  border-bottom: thin double gray;
}
.stocktable tbody tr {
  border: solid gray;
  border-width: 0 0 thin;
}
.stocktable tbody td {
  height: 40px;
  padding: 10px 0;
  vertical-align: top;
}

.stocktable .item_price {
  text-align: right;
}
.stocktable .item_quantity,
.stocktable .item_date {
  text-align: center;
}
